import React, {useState, useEffect} from 'react'
import './portfolio.css'
import 'bootstrap/dist/css/bootstrap.min.css';
import PortfoliopButtons from './PortfoliopButtons';
import axios from "axios";



const  Portfolio = () => {


    const [error, setError] = useState(null);
    const [isLoaded, setIsLoaded] = useState(false);
    const  [portfolios, setPortfolios] = useState([]);

 






   const  projectType = (clickedPorjectType) => {
          const newPortfolio = portfolios.filter((portfolioItem) => portfolioItem.type === clickedPorjectType)
          setPortfolios(   newPortfolio  )
        }



    // const getPortfData = "http://localhost:3009/portfolioDatas";

    useEffect(() => {
        fetch("http://localhost:3010/portfolioDatas")
          .then(res => res.json())
          .then(
            (result) => {
              setIsLoaded(true);
              setPortfolios(result);
            },
      

            (error) => {
              setIsLoaded(true);
              setError(error);
            }
          )
      }, [])


    if(error){
         return  <p> Error: {error.message}  </p>
    }

    else if(!isLoaded){ 
        return <div>Datas are dowloading...</div>
    }

    else{
         return(
                    <div id="portfolio" className="portfolio">

                    <div className="container">

                        <div className="row justify-content-center">
                            <div className="col-lg-8">
                                <div className="portfolio-title container-titles text-center">
                                    <h2>Portfolio</h2>
                                </div>
                            </div>
                        </div>



                        <div className="row portfolio-btns">
                            <div className="col-lg-12   ">
                            {/* butonlar gelecek */}
                             <PortfoliopButtons projectType={projectType}/>
                            </div>
                        </div>








                        <div className="row  ">
                             {portfolios.map((portfolio) => (
                                 <div className="col-md-6 col-lg-4 p-3  " key={portfolio.id} >
                                          <div className="portofilo-item text-capitalize">
                                          
                                          <div className="card-body">
                                              <div className="logoContent">
                                                    <i className={`logo ${portfolio.logo} `}></i>
                                              </div>
                                              <p className="card-title">{portfolio.name}</p>
                                              <a href={portfolio.ur} className="projectBtn">go to project</a>
                                          </div>
                                            
                                          
                                          </div>
                                 </div>
                             ))}
                        </div>

                    </div>

                    </div>
         )
    }

   
}

export default Portfolio
