import React, {useState} from 'react'

const PortfoliopButtons = ({projectType}) => {

    /*---------app website prowebsite buttonları----------*/
    const buttons = [
        {id: "app",        btnName:"app",  show:"app"},
        {id:  "website",   btnName:"website", show:"website"},
        {id: "proWebsite", btnName:"proWebsite", show:"prowebsite"}

  ]
const  [selected, setSelected] = useState("app");
/*---------app website prowebsite buttonları----------*/



    return (
    
                <ul>
                {buttons.map(btn => (
                    <li key={btn.id} //üzerine tıkladığım  seçilen .portBtn.active classı olsun 
                        className={selected === btn.id ?"portBtn active" :"portBtn"}
                        onClick={() => setSelected(btn.btnName)}
                        
                        >
                    
                        <p className="btn-name">{btn.btnName}</p>
                    </li>
                ))}
                </ul>

    )
}

export default PortfoliopButtons


